// src/app/layout.tsx
import type { Metadata } from "next";
import { Inter, Orbitron } from "next/font/google"; // Orbitron import කිරීම
import "./globals.css";
import Navbar from "@/components/Navbar";
import SmoothScroll from "@/components/SmoothScroll"; // SmoothScroll import කිරීම
import CustomCursor from "@/components/CustomCursor"; // CustomCursor import කිරීම

// Font variables සකස් කිරීම
const inter = Inter({ subsets: ["latin"], variable: "--font-inter" });
const orbitron = Orbitron({ subsets: ["latin"], weight: ['400', '700'], variable: "--font-orbitron" });

export const metadata: Metadata = {
  title: "NEXENTIA '25",
  description: "The official website for NEXENTIA '25 by JICTS.",
};

export default function RootLayout({ children }: Readonly<{ children: React.ReactNode; }>) {
  return (
    <html lang="en">
      {/* Font variables, body එකට යෙදීම */}
      <body className={`${inter.variable} ${orbitron.variable} font-sans bg-black`}>
        <CustomCursor />
        <SmoothScroll />
        <Navbar />
        {children}
      </body>
    </html>
  );
}